import React, { Component } from 'react';
import './App.css';

import ListRow from './ui/ListRow.js';
import ButtonNavigation from './ui/ButtonNavigation.js';
import SearchField from './ui/SearchField.js';
import Categories from './ui/Categories.js';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = { // когда просто писать ключ: значение, а когда "ключ": значение
      content: [],
      loading: false,
      url: '',
      category: 'Action',
      sort: {
        sortOn: '',
        direction: ''
      },
      query: '',
      page: 1,
    };
    this.myFun = this.myFun.bind(this) //почему тут надо биндить, а остальные которые тоже используют зис сетСтэйт можно не биндить?

  }

  changePage(page) {
    if (page !== this.state.page){
      this.setState({page});
      this.loadContent(`${this.state.url}&page=${page}`);
    }
  }

  sortFun(selector) { // если много объектов сортировки, то как сортировать лучше(про кучу ифов или свитч кейсов)?
    let newContent = [...this.state.content];
    if (this.state.sort.direction === 'up' && selector === this.state.sort.sortOn) {  // 100% костыль и надо как-нибудь через toggle или еще как-нибудь
      let newSort = {
        sortOn: selector,
        direction: 'down'
      }
      if (selector === 'year') {
        newContent.sort((a,b)=>{
          if (a.year > b.year) return 1;
          if (a.year < b.year) return -1;
        })
      } 
      else if(selector === 'rating'){
        newContent.sort((a,b)=>{
          if (a.rating > b.rating) return 1;
          if (a.rating < b.rating) return -1;
        })
      }
      this.setState({sort: newSort});
    } else { 
      if (selector === 'year') {
        newContent.sort((a,b)=>{
          if (a.year > b.year) return -1;
          if (a.year < b.year) return 1;
        })
      } 
      else if(selector === 'rating'){
        newContent.sort((a,b)=>{
          if (a.rating > b.rating) return -1;
          if (a.rating < b.rating) return 1;
        })
      }
      let newSort = {
        sortOn: selector,
        direction: 'up'
      }
      this.setState({sort: newSort});
    }    // конец костыля 
    this.setState({content: newContent});
  }

  urlMaker(url, query = '', page = 1) {
    this.setState({query})
    this.setState({page: 1});
    if (url !== this.state.url) this.setState({url});
    let fullUrl = `${url}&page=${page}`;
    this.loadContent(fullUrl);
  }

  loadContent(url) {
    this.setState({loading: true});
    console.log(url)
    fetch(url,{
      headers : {
        'Content-Type': 'application/json',
        'trakt-api-version': '2',
        'trakt-api-key': '24121afa0914eaddbfa752b63590b6021628bc7c3bbc374a4116963ccadefcdf'
      } 
    })
    .then(response => response.json().then(arr => { // загрузка контента
      let newArrContent = [];
      this.state.query ? (arr.map(item => {
        newArrContent.push(item.show)
      }))
            : (newArrContent = [...arr]);
      console.log(newArrContent)
      this.setState({content: newArrContent});
      return newArrContent;
    }))
    .then(data => { // загрузка картинок
      let prevContent = [...this.state.content];
      data.map((item,index) => {
        fetch(`http://webservice.fanart.tv/v3/tv/${item.ids.tvdb}?api_key=408b22edfb0465364b031d789388adb3`)
        .then(image => { 
          image.json().then(imageUrl => {
            let newItem = Object.assign({}, item)
            console.log(newItem);
            console.log(imageUrl);
            if (imageUrl.tvposter) { // Перебор большинства возможных значений нахождения Url картинки в JSON объекте
              newItem.url = imageUrl.tvposter[0].url;
            } 
            else if (imageUrl.clearlogo) {
              newItem.url = imageUrl.clearlogo[0].url;
            }
            else if (imageUrl.hdtvlogo) {
              newItem.url = imageUrl.hdtvlogo[0].url;
            }
            else if (imageUrl.status === 'error'){
              newItem.url = 'https://vignette.wikia.nocookie.net/citrus/images/6/60/No_Image_Available.png/revision/latest?cb=20170129011325';
            }
            else{
              newItem.url = 'https://cs.pikabu.ru/post_img/big/2013/11/22/5/1385100645_1399473065.jpg';
            }
            prevContent.splice(index,1,newItem);
            this.setState({content: prevContent})
            console.log(prevContent)
            if (this.state.loading) this.setState({loading: false});
          })
        })
      })
    })
  }

  myFun() {
    //this.setState({loading: !false})
  }

  componentDidMount() {
    this.urlMaker('https://api.trakt.tv/shows/popular?extended=full&limit=10&page=1&genres=action');
  }
  
render() {
  const { content, page, loading, category, query } = this.state;
    return(
      <div className="app">
        <SearchField title={query ? query : category} search={(url, query) => {this.urlMaker(url,query)}}/>
          <Categories onClick={(url,category, query = '') =>{this.urlMaker(url,query); this.setState({category})}}/>
        { (loading) ? //если идет загрузка контента
          <div className="wrap-loader"><div className="lds-facebook"><div></div><div></div><div></div></div></div>
                    :  //если контент загружен
          <div className="content-page">
            <table>
              <thead>
                <tr>
                  <th>#</th>
                  <th>Poster</th>
                  <th>Title/Description</th>
                  <th className="sort"
                      onClick={()=> this.sortFun('year')}>Year</th>
                  <th className="sort"
                      onClick={()=> this.sortFun('rating')}>Rating</th>
                </tr>
              </thead>
              <tbody>
                {content.map((item, index) => 
                  <ListRow key={item.ids.tvdb} 
                           title={item.title} 
                           year={item.year} 
                           id={item.url} 
                           number={index}
                           rating={item.rating}
                           page={page}
                           overview={item.overview}
                           reload={() => { this.setState({content}); console.log("lfbs")}} />
                )}
              </tbody>
            </table>
            <ButtonNavigation curPage={page} onChange={(page) => this.changePage(page)}/>
          </div>
        }
      </div>
    )
  }
}

export default App;



