import { 
  ButtonToolbar,
  ButtonGroup,
  Button,
} from 'react-bootstrap';
import React from 'react';

const ButtonNavigation = ({curPage, onChange}) => {
	if(curPage<4){
		return(
			<div className="wrapper-button-page-nav">
				<ButtonToolbar>
				  <ButtonGroup>
				    <Button bsSize="large" 
				    				bsStyle={(curPage === 1) ? "success" : "default"}
				    				onClick={() => onChange(1)}>1</Button>
				    <Button bsSize="large" 
				    				bsStyle={(curPage === 2) ? "success" : "default"}
				    				onClick={() => onChange(2)}>2</Button>
				    <Button bsSize="large" 
				    				bsStyle={(curPage === 3) ? "success" : "default"}
				    				onClick={() => onChange(3)}>3</Button>
				    <Button bsSize="large" 
				    				bsStyle={(curPage === 4) ? "success" : "default"}
				    				onClick={() => onChange(4)}>4</Button>
				    <Button bsSize="large" 
				    				bsStyle={(curPage === 5) ? "success" : "default"}
				    				onClick={() => onChange(5)}>5</Button>
				    <Button bsSize="large" 
				    				bsStyle={(curPage === 6) ? "success" : "default"}
				    				onClick={() => onChange(6)}>6</Button>
				    <Button bsSize="large" 
				    				className="button-page-nav"
				    				bsStyle={(curPage === 7) ? "success" : "default"}
				    				onClick={() => onChange(7)}>7</Button>
				  </ButtonGroup>
				</ButtonToolbar>
			</div>

		)
	}
	return(
		<div className="wrapper-button-page-nav">
			<ButtonToolbar>
			  <ButtonGroup>
			    <Button bsSize="large"
			    				className="button-page-nav"
				    			onClick={() => onChange(curPage-3)}>{curPage-3}</Button>
			    <Button bsSize="large"
			    				className="button-page-nav"
				  				onClick={() => onChange(curPage-2)}>{curPage-2}</Button>
			    <Button bsSize="large"
			    				className="button-page-nav"
				    			onClick={() => onChange(curPage-1)}>{curPage-1}</Button>
			  </ButtonGroup>

			  <ButtonGroup>
			    <Button bsSize="large"
			    				bsStyle="success"
				    			onClick={() => onChange(curPage)}>{curPage}</Button>
			  </ButtonGroup>

			  <ButtonGroup>
			    <Button bsSize="large"
			    				className="button-page-nav"
				    			onClick={() => onChange(curPage+1)}>{curPage+1}</Button>
			    <Button bsSize="large"
			    				className="button-page-nav"
				    			onClick={() => onChange(curPage+2)}>{curPage+2}</Button>
			    <Button bsSize="large"
			    				className="button-page-nav"
				    			onClick={() => onChange(curPage+3)}>{curPage+3}</Button>
			  </ButtonGroup>
			</ButtonToolbar>
		</div>
	)
}

export default ButtonNavigation;

/*
<ButtonToolbar>
  <ButtonGroup>
    <Button bsSize="large">1</Button>
    <Button bsSize="large">2</Button>
    <Button bsSize="large">3</Button>
  </ButtonGroup>

  <ButtonGroup>
    <Button bsSize="large" bsStyle="success">5</Button>
  </ButtonGroup>

  <ButtonGroup>
    <Button bsSize="large">6</Button>
    <Button bsSize="large">7</Button>
    <Button bsSize="large">8</Button>
  </ButtonGroup>
</ButtonToolbar>
*/


